<template>
  <div class="BookDetails">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <div class="detail">
      <div class="detail-box">
        <img class="auto-img" :src="DetaImg" alt="" />
      </div>
      <div class="detail-content">
        <div class="title">
          {{ DetailsData.title }}
        </div>
        <div class="type">
          <div class="type-t">
            {{ DetailsData["pris:book"].category }}
          </div>
           <div class="s">|</div>
           <div class="type-name">
             {{ DetailsData.author.name }}
           </div>
        </div>
        <div class="score">
          <van-rate
            readonly
            v-model="rateValue"
            :size="10"
            color="#f8bd12"
            void-icon="star"
            void-color="#eee"
          />
        </div>
        <div class="count">
          <div class="wordsCount">{{ num }}</div>
          <div class="clicksCount">
            {{ DetailsData["pris:subscribe"].clicksCount }}点击
          </div>
        </div>
        <div class="text-count">{{DetailsData["pris:book"].wprice}}阅读点/千字</div>
      </div>
    </div>
    <div class="text-content">
      {{DetailsData.summary}}
    </div>
    <div class="authorization">授权方:<div class="auth">{{DetailsData["pris:book"].authorizer}}</div></div>
    <div class="zptype">作品类型:{{DetailsData["pris:book"].paydesc}}</div>
    <van-divider :style="{ padding: '0 10px' }" />

    <div class="catalogue" @click="gitSkipCat">
      <div class="catalogue-box">
        <div class="catalogue-img">
          <img class="auto-img" src="../assets/ml.png" alt="">
        </div>
        <div class="catalogue-title">
          <div class="catalogue-date">{{timeDate[0]+'月'+timeDate[1]+'日'}}</div>
          <div class="catalogue-new">更至:{{newWordsData}}</div>
        </div>
        <div class="state">连载中<van-icon name="arrow" /></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BookDetails",
  data() {
    return {
      rateValue: "",
      DetailsId: "",
      DetaImg: "",
      DetailsData: [],
      DetailsTitle: "",
      timeDate:'',
      num: 0,
      newWordsData:[],
    };
  },
  created() {
    this.DetailsTitle = this.$route.params.title;
    this.DetailsId = this.$route.params.id;
    this.getDetailsData();
    this.getNewWords();
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },

    gitSkipCat(){
      this.$router.push({name:'BookCatalogue',params:{id:this.DetailsId}})
    },

    getWords() {
      this.num = Number(this.DetailsData["pris:book"].words);
      if (this.num == 0) {
        return this.num + "";
      } else if (this.num > 1 && this.num < 10000) {
        return this.num + "";
      } else {
        return (this.num =(Math.round(this.num / 10000).toFixed(1)) + "万");
      }
    },

  async  getDetailsData() {
   await  this.axios.get("https://apis.netstart.cn/yunyuedu/book/getsub.json?id=" +
          this.DetailsId +
          "&title=" +
          this.DetailsTitle).then(re => {
          console.log(re);
          this.DetailsData = re.feed.entry;
          this.DetaImg = this.DetailsData.link[1].href;
          this.rateValue = parseInt(this.DetailsData["pris:ranking"].value);
          this.timeDate = this.DetailsData['pris:book'].updated
          this.timeDate = this.timeDate.split('T')
          this.timeDate = (this.timeDate[0].split('-')).splice(1,2);
          this.getWords();
        })
        .catch((err) => {
          console.log(err);
        });
    },
   async getNewWords(){
    await  this.axios.get("https://apis.netstart.cn/yunyuedu/book/simpleInfo.json?id=" + this.DetailsId).then(re => {
          this.newWordsData = re.item.latestArticleTitle
        })
        .catch((err) => {
          console.log(err);
        });
    }
  },
};
</script>

<style lang="scss" scoped>

.catalogue{
  padding: 0 10px;
  .catalogue-box{
    display: flex;
    align-items: center;
    .catalogue-img{
      width: 16px;
      height: 16px;
    }
  }
  .catalogue-title{
    display: flex;
    align-items: center;
    transform: scale(0.9);
  }
  .state{
    transform: scale(0.9);
    margin-left: auto;
  }
}

.detail {
  display: flex;
  padding: 10px 10px;
  .detail-box {
    width: 110px;
    height: 145px;
  }
  .detail-content {
    margin-left: 5px;
    .title{
      font-size: 14px;
    }
    .type {
      padding: 10px 0;
      display: flex;
      align-items: center;
      font-size: 12px;
      .s{
        padding: 0 5px;
      }
      .type-t{
        color: #979797;
      }
      .type-name{
        color: rgb(0, 162, 255);
      }
    }
    .count {
      display: flex;
      color: #979797;
      font-size: 12px;
      .wordsCount {
        margin-right: 10px;
      }
    }
    .score{
      padding-bottom: 5px;
    }
    .text-count{
      margin-top: 10px;
      color: #979797;
      font-size: 12px;
    }
  }
}
.text-content{
  line-height: 18px;
  padding: 10px 10px 0 10px;
  color: #2d2d2d;
  overflow: hidden;
  text-overflow: ellipsis; // text-overflow css3的属性，当文本溢出时，显示省略号
  display: -webkit-box;
  -webkit-line-clamp: 3; // 设置两行文字溢出
  -webkit-box-orient: vertical;
}
.authorization{
  padding: 10px;
  display: flex;
  align-items: center;
  color: #979797;
  .auth{
    margin-left: 3px;
    color: rgb(0, 162, 255);;
  }
}
.zptype{
  padding: 0 10px;
  color: #979797;
}
</style>
